/*-- -------------------------- -->
<---          Gallery           -->
<--- -------------------------- -*/

/* Mobile - 360px */
@media only screen and (min-width: 0rem) {
	#gallery-s-1152 {
		padding: var(--sectionPadding);
	}
	#gallery-s-1152 .cs-container {
		width: 100%;
		/* changes to 1280px at large desktop */
		max-width: 59rem;
		margin: auto;
		display: flex;
		flex-direction: column;
		align-items: center;
		/* 48px - 64px */
		gap: clamp(3rem, 6vw, 4rem);
	}
	#gallery-s-1152 .cs-content {
		/* set text align to left if content needs to be left aligned */
		text-align: center;
		width: 100%;
		display: flex;
		flex-direction: column;
		/* centers content horizontally, set to flex-start to left align */
		align-items: center;
	}

	#gallery-s-1152 .cs-title {
		margin: 0;
	}
	#gallery-s-1152 .cs-gallery {
		width: 100%;
		/* changes to 100% at tablet */
		max-width: 31.25rem;
		padding: 0;
		margin: 0;
		display: grid;
		grid-template-columns: repeat(12, 1fr);
		/* 16px - 20px */
		gap: clamp(1rem, 1.5vw, 1.25rem);
	}
	#gallery-s-1152 .cs-image {
		/* 260px - 360px */
		min-height: clamp(16.25rem, 60vw, 20rem);
		border-radius: 1rem;
		/* clips the image corners */
		overflow: hidden;
		display: block;
		grid-column: span 12;
		grid-row: span 1;
		position: relative;
	}
	#gallery-s-1152 .cs-image img {
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		/* makes it act like a background image */
		object-fit: cover;
	}
}
/* Tablet - 700px */
@media only screen and (min-width: 43.75rem) {
	#gallery-s-1152 .cs-gallery {
		max-width: 100%;
		grid-template-rows: 1fr;
	}
	#gallery-s-1152 .cs-image {
		grid-column: span 4;
	}
	#gallery-s-1152 .cs-image:nth-of-type(4),
	#gallery-s-1152 .cs-image:nth-of-type(5) {
		grid-column: span 6;
	}
}
/* Large Desktop - 1300px */
@media only screen and (min-width: 81.25rem) {
	#gallery-s-1152 .cs-container {
		max-width: 80rem;
	}
	#gallery-s-1152 .cs-gallery {
		grid-template-columns: repeat(5, 1fr);
	}
	#gallery-s-1152 .cs-image {
		grid-column: span 1;
	}
	#gallery-s-1152 .cs-image:nth-of-type(4),
	#gallery-s-1152 .cs-image:nth-of-type(5) {
		grid-column: span 1;
	}
}
